#footer {
  background: #00030C;
  padding: 5rem 0;
}
.footer-top {
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 1.5rem;
  }
  &__left {
    display: flex;
    align-items: center;
    gap: 1.5rem;
    .footer-top__socials {
      display: none;
    }
  }
  &__right {
    display: flex;
    gap: 4.375rem;
  }
  &__logo {
    width: 18.5625rem;
    display: block;
    img {
      width: 100%;
    }
  }
  &__text {
    font-size: 1rem;
    line-height: 130%;
    color: rgb(0, 207, 252);
  }
  &__group {
    display: flex;
    align-items: start;
    gap: 2rem;
  }
  &__socials {
    display: flex;
    gap: 0.875rem;
  }
  &__social {
    width: 2.25rem;
    height: 2.25rem;
    display: flex;
    justify-content: center;
    align-items: center;
    background: rgb(0, 207, 252);
    border: 0.0625rem solid transparent;
    border-radius: 50%;
    &:hover {
      background: rgb(255, 255, 255);
      svg path {
        fill: rgb(0, 207, 252);
      }
    }
    &:active {
      background: rgb(0, 3, 12);
      border-color: rgb(0, 207, 252);
      svg path {
        fill: rgb(255, 255, 255);
      }
    }
  }
  &__location {
    font-size: 0.875rem;
    line-height: 130%;
    color: rgb(255, 255, 255);
    a {
      display: flex;
      gap: 0.625rem;
      margin-bottom: 0.625rem;
      i {
        width: 0.875rem;
        flex-shrink: 0;
      }
      i svg {
        width: 100%;
        height: auto;
      }
    }
    a:hover {
      color: rgb(0, 207, 252);
    }
    p {
      padding-left: 1.5rem;
      margin: 0;
    }
    p span {
      color: rgb(0, 207, 252);
    }
  }
  &__email {
    display: flex;
    align-items: center;
    gap: 0.5rem;
    font-size: 0.875rem;
    color: rgb(0, 207, 252) !important;
    &:hover {
      color: rgb(255, 255, 255) !important;
      svg path {
        fill: rgb(255, 255, 255);
      }
      span {
        border-color: rgb(255, 255, 255);
      }
    }
    i {
      flex-shrink: 0;
      width: 1.25rem;
      svg {
        width: 100%;
        height: auto;
      }
    }
    span {
      border-bottom: 0.0625rem dashed rgb(0, 207, 252);
    }
  }
  &__group2 {
    flex-shrink: 0;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
  }
  &__phone {
    font-family: NotoSans;
    font-size: 1.25rem;
    line-height: 130%;
    font-weight: 700;
    color: rgb(255, 255, 255);
    &:hover {
      color: rgb(0, 207, 252);
    }
  }
  &__btn {
    background: transparent;
    border-bottom: 0.0625rem dashed rgb(0, 207, 252);
    font-size: 0.875rem;
    line-height: 130%;
    color: rgb(0, 207, 252);
    margin-top: 0.625rem;
    &:hover {
      border-color: #fff;
      color: #fff;
    }
    span {
      min-width: unset;
      padding: 0;
      overflow: unset;
    }
  }
}
.footer-bottom {
  padding-top: 5rem;
  &__inner {
    display: flex;
    justify-content: space-between;
    gap: 1rem 1.5rem;
  }
  .b2b {
    width: 10.9375rem;
    flex-shrink: 0;
    img {
      width: 100%;
    }
  }
  &__text {
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-end;
    align-items: center;
    gap: 1rem;
    p {
      display: inline-flex;
      flex-wrap: wrap;
      gap: 0.5rem;
      font-size: 0.875rem;
      line-height: 130%;
      margin: 0;
    }
    p:not(:last-child) {
      &:after {
        content: '/';
        display: inline-block;
        margin-left: 0.5rem;
      }
    }
    a:hover {
      color: rgb(0, 207, 252);
    }
  }
}

@media screen and (max-width: 1600px) {
  .footer-top {
    &__right {
      gap: 2rem;
    }
    &__logo {
      width: 14rem;
    }
    &__text {
      font-size: 0.875rem;
    }
  }
}

@media screen and (max-width: 1399px) {
  .footer-top {
    &__inner {
      align-items: start;
    }
    &__left {
      flex-direction: column;
      align-items: start;
      gap: 1rem;
    }
    &__right {
      gap: 1.5rem;
    }
    &__group {
      gap: 1.5rem;
    }
  }
}

@media screen and (max-width: 1023px) {
  #footer {
    padding: 2rem 0;
  }
  .footer-top {
    &__group {
      flex-direction: column;
      gap: 1rem;
    }
    &__location {
      p {
        padding-left: 0;
      }
    }
  }
  .footer-bottom {
    padding-top: 2rem;
    &__inner {
      flex-wrap: wrap;
    }
    &__text {
      justify-content: start;
    }
  }
}

@media screen and (max-width: 768px) {
  .footer-top {
    &__inner {
      gap: 1rem;
    }
    &__left {
      .footer-top__socials {
        display: flex;
      }
    }
    &__socials {
      display: none;
    }
  }
  .footer-bottom {
    &__text {
      flex-direction: column;
      align-items: start;
      p {
        &:after {
          display: none !important;
        }
      }
    }
  }
}

@media screen and (max-width: 650px) {
  .footer-top {
    &__right {
      flex-direction: column;
      gap: 1rem;
    }
    &__group2 {
      align-items: start;
    }
  }
}

@media screen and (max-width: 499px) {
  .footer-top {
    &__logo {
      width: 11rem;
    }
    &__text {
      font-size: 0.75rem;
    }
  }
}

@media screen and (max-width: 399px) {
  .footer-top {
    &__inner {
      flex-direction: column;
    }
  }
}

