h2 {
  font-size: 2.25rem;
  line-height: 130%;
  margin: 0;
  .text_color {
    color: rgb(0, 207, 252);
  }
}
.btn-blue {
  position: relative;
  background: transparent;
  &:after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgb(0, 207, 252);
    clip-path: polygon( 99.587% 86.117%,93.936% 98.387%,0.413% 98.387%,0.413% 56.135%,0.413% 13.883%,5.597% 1.613%,99.587% 1.613%,99.587% 86.117% );
    pointer-events: none;
  }
  &:before {
    content: '';
    position: absolute;
    z-index: 2;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: calc(100% - 0.25rem);
    height: calc(100% - 0.25rem);
    background: linear-gradient(85.41deg, rgb(5, 174, 211) 0.26%,rgb(25, 56, 110) 111.235%);
    clip-path: polygon( 99.587% 86.117%,93.936% 98.387%,0.413% 98.387%,0.413% 56.135%,0.413% 13.883%,5.597% 1.613%,99.587% 1.613%,99.587% 86.117% );
    pointer-events: none;
    transition: all 0.3s;
  }
  &:hover {
    &:before {
      background: rgb(0, 207, 252);
    }
  }
  &:active {
    &:before {
      background: rgb(0, 3, 12);
    }
  }
  span {
    position: relative;
    z-index: 3;
    min-width: unset;
    padding: 1.125rem 2rem;
    overflow: unset;
    font-family: NotoSans;
    font-size: 1.125rem;
    line-height: 130%;
    font-weight: 700;
    text-transform: uppercase;
    color: rgb(255, 255, 255);
  }
  .box-shadow {
    position: absolute;
    z-index: 0;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 98%;
    height: 75%;
    background: linear-gradient(85.41deg, rgb(5, 174, 211) 0.26%,rgb(25, 56, 110) 111.235%);
    box-shadow: 0 0.25rem 1.875rem 0.25rem rgba(255, 255, 255, 0.34);
    pointer-events: none;
  }
}
.btn-white {
