.navigation {
  padding: 3.125rem 0;
  &__list {
    display: grid;
    grid-template-columns: 100%;
    gap: 1rem;
    li a {
      font-size: 1.5rem;
      line-height: 130%;
      color: #fff;
      &:hover {
        color: #007aff;
      }
    }
  }
}

@media screen and (max-width: 1600px) {
  h2 {
    font-size: 2rem;
  }
}

@media screen and (max-width: 1023px) {
  h2 {
    font-size: 1.75rem;
  }
  .btn-blue,
  .btn-white{
    span {
      font-size: 1rem;
    }
  }
  .swiper-pagination {
    font-size: 2.5rem;
    span:nth-child(2) {
      font-size: 1.25rem;
    }
  }
}