.main-section {
  z-index: 3;
  color: #fff;
  padding: 9.375rem 0 6.25rem;
  &:after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: calc(100% + 6.25rem);
    background-image: url("../img/main-bg.webp");
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    pointer-events: none;
  }
  &__inner {
    position: relative;
  }
  &__wrap {
    position: relative;
    z-index: 2;
    max-width: 38.75rem;
  }
  &__info {
    position: relative;
    display: inline-flex;
    border: 0.125rem solid rgb(0, 207, 252);
    box-shadow: 0 0.25rem 1.4375rem 0 rgba(255, 255, 255, 0.34);
    clip-path: polygon(4% 0,100% 0,100% 30%,100% 77%,96% 100%,0 100%,0 70%,0 23%);
    padding: 1rem 2rem;
    font-size: 1.25rem;
    line-height: 130%;
    font-weight: 700;
    overflow: hidden;
    &:after {
      content: '';
      position: absolute;
      top: 0.3125rem;
      left: -0.375rem;
      transform: rotate(-45deg);
      width: 1.5rem;
      height: 0.125rem;
      background: rgb(0, 207, 252);
      pointer-events: none;
    }
    &:before {
      content: '';
      position: absolute;
      bottom: 0.3125rem;
      right: -0.375rem;
      transform: rotate(-45deg);
      width: 1.5rem;
      height: 0.125rem;
      background: rgb(0, 207, 252);
      pointer-events: none;
    }
  }
  h1 {
    font-size: 4rem;
    line-height: 130%;
    margin: 3rem 0 0 0;
    .text_color {
      color: rgb(0, 207, 252);
    }
  }
  p {
    font-family: NotoSans;
    font-size: 2rem;
    line-height: 130%;
    font-weight: 700;
    color: rgb(255, 255, 255);
    margin: 2rem 0 0 0;
    span {
      color: rgb(0, 207, 252);
    }
  }
  &__group {
    display: flex;
    align-items: center;
    gap: 1.5rem;
    margin-top: 3rem;
  }
  &__info2 {
    font-size: 1rem;
    line-height: 130%;
    font-weight: 700;
  }
  &__items {
    position: relative;
    display: grid;
    grid-template-columns: repeat(3, calc(100% / 3));
    margin-top: 6rem;
    &:after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: linear-gradient(90deg, rgba(0,207,252,1) 0%, rgba(143,143,143,1) 91%);
      //background: linear-gradient(90deg, rgba(0,207,252,1) 0%, rgba(143,143,143,1) 70%);
      backdrop-filter: blur(4.5rem);
      clip-path: polygon( 99.991% 87.321%,96.64% 100%,66.679% 100%,33.367% 100%,0% 100%,0% 56.339%,0% 12.679%,3.074% 0%,33.367% 0%,66.679% 0%,99.991% 0%,99.991% 87.321% );
      pointer-events: none;
    }
    &:before {
      content: '';
      position: absolute;
      z-index: 1;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: calc(100% - 0.25rem);
      height: calc(100% - 0.25rem);
      background: linear-gradient(180.00deg, rgba(0, 207, 252, 1) -77.406%,rgba(26, 26, 26, 1) 95.608%);
      clip-path: polygon( 99.991% 87.321%,96.64% 100%,66.679% 100%,33.367% 100%,0% 100%,0% 56.339%,0% 12.679%,3.074% 0%,33.367% 0%,66.679% 0%,99.991% 0%,99.991% 87.321% );
      pointer-events: none;
    }
  }
  &__item {
    position: relative;
    z-index: 1;
    display: flex;
    align-items: center;
    padding: 2rem 1rem 2rem 0;
    &:not(:last-child) {
      border-right: 0.125rem solid #8F8F8F;
    }
    &-img {
      max-width: 13.75rem;
      flex-shrink: 0;
    }
    &-text {
      max-width: 16.5625rem;
      text-align: center;
      font-size: 1.25rem;
      line-height: 130%;
      font-weight: 700;
      span {
        color: rgb(0, 207, 252);
      }
    }
  }
  &__img {
    position: absolute;
    top: -4rem;
    left: 50%;
    transform: translate(-50%, 0);
    width: 12.5rem;
    animation: img 8s infinite linear;
    &:after {
      content: '';
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 6.25rem;
      height: 6.25rem;
      filter: blur(7.8125rem);
      background: rgb(255, 194, 162);
      border-radius: 50%;
      pointer-events: none;
    }
    img {
      position: relative;
      z-index: 1;
      width: 100%;
    }
  }
  &__img2 {
    position: absolute;
    top: 22rem;
    right: 11.5rem;
    width: 10.9375rem;
    animation: img 8s infinite linear;
    &:after {
      content: '';
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 6.25rem;
      height: 6.25rem;
      filter: blur(7.8125rem);
      background: rgb(255, 194, 162);
      border-radius: 50%;
      pointer-events: none;
    }
    img {
      position: relative;
      z-index: 1;
      width: 100%;
    }
  }
  &__img3 {
    position: absolute;
    right: 1rem;
    bottom: -2rem;
    width: 14.375rem;
    animation: img 8s infinite linear;
    img {
      width: 100%;
    }
  }
  &__img4 {
    position: absolute;
    top: 22rem;
    left: 60%;
    transform: translate(-50%, 0);
    width: 10rem;
    animation: img 6s infinite linear;
    &:after {
      content: '';
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 6.25rem;
      height: 6.25rem;
      filter: blur(7.8125rem);
      background: rgb(255, 194, 162);
      border-radius: 50%;
      pointer-events: none;
    }
    img {
      position: relative;
      z-index: 1;
      width: 100%;
    }
  }
}
.strategy {
  color: #fff;
  padding: 6.25rem 0;
  &:after {
    content: '';
    position: absolute;
    top: 50%;
    left: 0;
    transform: translate(0, -50%);
    width: 100%;
    height: calc(100% + 23.125rem);
    background-image: url("../img/strategy-bg.webp");
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    pointer-events: none;
  }
  &--active {
    &:after {
      display: none;
    }
  }
  &__inner {
    max-width: 60.3125rem;
    margin-left: auto;
  }
  ul {
    max-width: 53.75rem;
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 1.375rem / 2));
    gap: 1.375rem;
    margin-top: 3rem;
    li {
      position: relative;
      font-size: 1.25rem;
      line-height: 130%;
      color: rgb(206, 206, 206);
      padding-left: 1.25rem;
      &:after {
        content: '';
        position: absolute;
        top: 0.5rem;
        left: 0;
        width: 0.5rem;
        height: 0.5rem;
        background: #ffffff;
        border-radius: 50%;
      }
      strong {
        font-weight: 700;
        color: #fff;
      }
    }
  }
  &__group {
    display: flex;
    align-items: center;
    gap: 1.5rem;
    margin-top: 3rem;
  }
  &__info {
    font-size: 1rem;
    line-height: 130%;
    font-weight: 700;
    span {
      color: rgb(0, 207, 252);
    }
  }
}
.quiz {
  position: relative;
  z-index: 2;
  display: none;
  .container-fluid {
    position: static;
  }
  &--active {
    display: block;
  }
  &__inner {
    max-width: 60.3125rem;
    margin-left: auto;
  }
  &-stages {
    position: relative;
    z-index: 1;
    display: flex;
    flex-wrap: wrap;
    align-items: stretch;
    gap: 0.0625rem;
  }
  &-stage {
    font-family: NotoSans;
    font-size: 1.75rem;
    line-height: 130%;
    font-weight: 700;
    color: rgb(0, 0, 0);
    background: rgb(255, 255, 255);
    padding: 0.625rem 2.25rem;
    span {
      display: none;
    }
    &--active {
      color: #fff;
      background: rgb(0, 207, 252);
      span {
        display: inline-block;
      }
    }
  }
  &-btns {
    position: relative;
    z-index: 1;
    display: flex;
    gap: 3rem;
    margin-top: 4rem;
  }
  &-prev,
  &-next {
    background: rgb(212, 212, 212);
    border-radius: 0.25rem;
    &:hover {
      background: #00CFFC;
      span {
        color: #fff;
      }
    }
    span {
      min-width: auto;
      padding: 1.125rem 2rem;
      font-family: NotoSans;
      font-size: 1.125rem;
      line-height: 130%;
      font-weight: 700;
      text-transform: uppercase;
      color: rgb(0, 0, 0);
    }
  }
  &-prev {
    display: none !important;
  }
  &-prev--active {
    display: flex !important;
  }
  &-next {
    background: rgb(255, 255, 255);
  }
  &-body {
    margin-top: 3.5rem;
  }
  &-step {
    display: none;
  }
  &-step--active {
    display: block;
  }
  &-step__bg {
    position: absolute;
    top: 50%;
    left: 0;
    transform: translate(0, -50%);
    width: 100%;
    height: calc(100% + 23.125rem);
    pointer-events: none;
    img {
      width: 100%;
      height: 100%;
    }
  }
  h2 {
    position: relative;
    z-index: 1;
  }
  &__subtitle {
    position: relative;
    z-index: 1;
    max-width: 43.75rem;
    font-size: 1.75rem;
    line-height: 130%;
    margin-top: 2rem;
    span {
      color: rgb(0, 207, 252);
    }
  }
  &__checkboxes {
    position: relative;
    z-index: 1;
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 2rem / 2));
    gap: 2rem;
    margin-top: 3rem;
  }
  &__checkbox {
    position: relative;
    input {
      position: absolute;
      z-index: 1;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      opacity: 0;
      margin: 0;
    }
    input:checked ~ label {
      background: rgb(0, 207, 252);
    }
    label {
      width: 100%;
      height: 100%;
      min-height: 5rem;
      background: rgba(0, 207, 252, 0.45);
      border: 0.0625rem solid rgb(0, 207, 252);
      border-radius: 0.25rem;
      font-size: 1.125rem;
      line-height: 130%;
      color: rgb(255, 255, 255);
      padding: 1.125rem 2rem;
      transition: all 0.3s;
      strong {
        font-weight: 700;
      }
    }
  }
  &-form {
    display: none;
    margin-top: 3.5rem;
  }
  &-form--active {
    display: block;
  }
  &-form__wrap {
    position: relative;
    z-index: 1;
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 2rem / 2));
    gap: 2rem;
    margin-top: 3rem;
  }
  &-form__group {
    input {
      width: 100%;
      height: 5rem;
      background: rgba(0, 207, 252, 0.45);
      border: 0.0625rem solid rgb(0, 207, 252);
      border-radius: 0.25rem;
      font-size: 1.125rem;
      line-height: 130%;
      color: rgb(255, 255, 255);
      padding: 1.125rem 2rem;
      &::placeholder {
        color: #D4D4D4 !important;
      }
    }
  }
  &-form__btn {
    background: #fff;
    border-radius: 0.25rem;
    margin-top: 2rem;
    &:hover {
      background: #00CFFC;
      span {
        color: #fff;
      }
    }
    span {
      min-width: auto;
      padding: 1.125rem 2rem;
      font-family: NotoSans;
      font-size: 1.125rem;
      line-height: 130%;
      font-weight: 700;
      text-transform: uppercase;
      color: rgb(0, 0, 0);
    }
  }
  .quiz-error {
    position: relative;
    z-index: 1;
    display: none;
    font-size: 1rem;
    line-height: 130%;
    font-weight: 500;
    color: #fff;
    margin-top: 0.625rem;
  }
  .quiz-error--active {
    display: block;
  }

}
