  background-repeat: no-repeat;
  background-position: top;
  background-size: cover;
  color: #fff;
  padding: 18.75rem 0 9.375rem;
  &__inner {
    max-width: 44.375rem;
  }
  h1 {
    font-size: 3rem;
    line-height: 130%;
    color: rgb(0, 207, 252);
  }
  p {
    max-width: 37rem;
    font-size: 2rem;
    line-height: 130%;
    margin: 2rem 0 0 0;
  }
  &__btn {
    margin-top: 4rem;
  }
  &__img {
    position: absolute;
    right: 0;
    bottom: 0;
    max-width: 75rem;
    pointer-events: none;
    img {
      width: 100%;
    }
  }
}
.mistake {
