    font-size: 3rem;
    line-height: 130%;
    color: rgb(0, 207, 252);
  }
  p {
    font-size: 2rem;
    line-height: 130%;
    margin: 2rem 0 0 0;
  }
  &__btn {
    margin-top: 4rem;
  }
  &__img {
    width: 100%;
  }
}


