  .team {
    padding: 2rem 0;
    &-card {
      &__name,
      &__post {
        font-size: 0.75rem;
      }
    }
  }
  .connection {
    padding: 2rem 0 6rem;
    &__img {
      top: unset;
      bottom: -5rem;
      transform: unset;
    }
    &__img2 {
      top: unset;
      bottom: -5rem;
      transform: unset;
    }
  }
  .gratitude {
    padding: 8rem 0 4rem;
    p {
      font-size: 1.125rem;
    }
  }
  .mistake {
    padding-top: 8rem;
    p {
      font-size: 1.125rem;
    }
  }
}

@media screen and (max-width: 499px) {
  .advantages {
    &__items {
      grid-template-columns: 100%;
    }
  }
  .advantages2 {
    &__item {
      &-number {
        width: 2rem;
        height: 2rem;
        font-size: 1rem;
      }
      &-text {
        font-size: 0.8125rem;
      }
    }
  }
}

@media screen and (max-width: 399px) {
  .main-section {
    &__img {
      right: -0.625rem;
      width: 4rem;
      &:after {
        width: 2rem;
        height: 2rem;
      }
    }
    &__img2 {
      right: -0.625rem;
      width: 4rem;
      &:after {
        width: 2rem;
        height: 2rem;
      }
    }
    &__img4 {
      width: 5rem;
    }
  }
  .advantages2 {
    &__item {
      &-text {
        font-size: 0.75rem;
      }
    }
  }
}