#header {
  position: absolute;
  z-index: 5;
  top: 0;
  left: 0;
  width: 100%;
  padding: 2rem 0 0 0;
  .header__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 1rem;
  }
  .header__logo {
    width: 20.8125rem;
    display: block;
    img {
      width: 100%;
    }
  }
  .header__group {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
  }
  .header__phone {
    font-family: NotoSans;
    font-size: 1.25rem;
    line-height: 130%;
    font-weight: 700;
    color: rgb(255, 255, 255);
    &:hover {
      color: rgb(0, 207, 252);
    }
  }
  .header__btn {
    background: transparent;
    border-bottom: 0.0625rem dashed rgb(0, 207, 252);
    font-size: 0.875rem;
    line-height: 130%;
    color: rgb(0, 207, 252);
    margin-top: 0.625rem;
    &:hover {
      border-color: #fff;
      color: #fff;
    }
    span {
      min-width: unset;
      padding: 0;
      overflow: unset;
    }
  }
}

@media screen and (max-width: 1023px) {
  #header {
    padding: 1.5rem 0 0 0;
  }
}

@media screen and (max-width: 576px) {
  #header {
    .header__logo {
      width: 12rem;
    }
    .header__phone {
      font-size: 1rem;
    }
  }
}

@media screen and (max-width: 399px) {
  #header {
    .header__logo {
      width: 9rem;
    }
    .header__phone {
      font-size: 0.875rem;
    }
  }
}